
import React from "react";
import Top from "./Pages/Top";
import Music from "./Pages/Jonas";
import Hobbies from "./Pages/Hobbies";
import Profile from "./Pages/Profile";

import logo1 from "./images/Jonas.jpg";

import logo2 from "./images/Matin.jpg";

import logo3 from "./images/Matin.jpg";

import SimpleImageSlider from "react-simple-image-slider";



import { HashRouter as Router, Route, Link } from 'react-router-dom'


const TabLink = ({ to, children, exact }) => (
  <Route
    path={to}
    exact={exact}
    children={({ match }) => {
      return (
        <li className={match ? 'is-active' : ''}>
          <Link to={to} style={{ color: match ? 'rgb(32,88,90)' : 'white' }}>
            {children}
          </Link>
        </li>
      )
    }}
  />
)


const App = () => {
  const images = [
        { url: logo1 },
        { url: logo2 },
        { url: logo3 },
    ];
  return (
    <Router>
    <section className="hero is-primary">
      <div className="hero-body">
        <h1 className="title">Self-introduction</h1>
      </div>
      
      <div className='hero-foot'>
          <nav className='tabs is- is-fullwidth'>
            <div className='container'>
              <ul>
                <TabLink to='/' exact>
                  Top
                </TabLink>
                <TabLink to='/Profile'>Profile</TabLink>
                <TabLink to='/Hobbies'>Hobbies</TabLink>
                <TabLink to='/Music'>Music</TabLink>
              </ul>
            </div>
          </nav>
        </div>
    </section>
  
    <div>
      <Route exact path="/" component={Top} />
      <Route path="/Profile" component={Profile} />
      <Route path="/Hobbies" component={Hobbies} />
      <Route path="/Music" component={Music} />
    </div>
    <br>
    </br>
    
  
    
    <footer className="footer">
      <div className="content has-text-centered">
        <p>&copy; 2020 Kyohei Kaneko</p>
      </div>
    </footer>
  </Router>
  );
};

/*
    <div className = "container">
        <a href ="/Self-introduction has-text-right">
          <div class="buttons ">
            <button class="button is-primary 	">
              Topに戻る
            </button>
          </div> 
          </a>
    </div>
*/

export default App;
