import logo2 from "../images/badk.jpg";
import logo3 from "../images/sno2.jpg";
import logo4 from "../images/myface4.jpg";
import logo5 from "../images/dis1.jpg";
import logo6 from "../images/dis2.jpg";
import logo7 from "../images/soc1.jpg";
import logo8 from "../images/soc2-1.jpg";
import React, { useEffect, useState, useRef } from "react";
